import React, { PropTypes } from 'react';
import { browserHistory, Link } from 'react-router';
import Popup from 'react-popup';
import styled from 'styled-components';

import ClothAlterations from './ClothAlterations';
import Alterations from './Alterations';

import { Wrapper, Block, Header2, Title2, Logo, Button3, Total, ClothesList, ErrorText, Cloth, Add, ClothText,
Title3, Highlight, TextLink, TextLinkSeparator, mobileThresholdPixels } from './styledComponents';
import Modal from './Modal';
import ProgressBar from './order/ProgressBar';
import LogoImg from '../assets/logo1.png';
import { trackEvent } from '../services/analytics';
import './popup.css';

const Center = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  @media (max-width: ${mobileThresholdPixels}) {
    flex-direction: column;
    align-items: center;
  }
`;

const hasClickedInsideModale = (evt) => {
  const flyoutElement = document.getElementById('modal-retouche');
  let targetElement = evt.target; // clicked element

  do {
    if (targetElement === flyoutElement) {
      return true;
    }
    // Go up the DOM
    targetElement = targetElement.parentNode;
  } while (targetElement);

  return false;
};

function isModalOpened(showChoicesCloth, showChoicesClothRef) {
  return !!showChoicesCloth && !!showChoicesClothRef;
}

function openPopup({ origin }) {
  trackEvent('click', `ouvrir-popup-aide-retouche_${origin}`);
  Popup.create({
    title: 'Aide',
    content: (<div>{`
      Cette estimation nous permet d'avoir une idée de la quantité de travail mais
      ce n'est pas grave si elle n'est pas exacte.
      En effet, votre tilliste pourra réajuster cette estimation lors de votre rdv.
      Vous pourrez par exemple ajouter d'autres vêtements à retoucher.
    `}<br /><br />{`
      Vous pouvez aussi nous poser vos questions par chat (en bas à droite de la fenêtre)
      ou par mail à contact@tilli.fr
    `}<br /><br />{`
      Vous ne savez vraiment pas quoi choisir ? Pas de problème, vous pouvez aussi passer cette étape 😉
    `}</div>),
    buttons: {
      right: [{
        text: 'Fermer',
        action() { Popup.close(); },
      }],
    },
  });
}

const Step2 = ({ clothes, toggleOrSetAlteration, total, showChoicesCloth, showChoicesClothRef, openedCategory,
openCategory, setChoicesCloth, close, showChoicesItemIndex, isButtonValid, goNext, hasError,
deleteCloth }) => (
  <div // eslint-disable-line jsx-a11y/no-static-element-interactions
    onClick={e => isModalOpened(showChoicesCloth, showChoicesClothRef) &&
      !hasClickedInsideModale(e) && close()}
  >
    <Modal show={isModalOpened(showChoicesCloth, showChoicesClothRef)}>
      {showChoicesCloth && showChoicesClothRef ?
        <div id="modal-retouche">
          <Title2>
            Choix des retouches pour {showChoicesCloth.name}
          </Title2>
          <Alterations
            clothRef={showChoicesClothRef}
            openedCategory={openedCategory}
            openCategory={openCategory}
            close={close}
            cloth={clothes[showChoicesItemIndex]}
            toggleOrSetAlteration={(alterationId, quantity) =>
              toggleOrSetAlteration(showChoicesItemIndex, alterationId, quantity)}
          />
          <Center style={{ paddingBottom: '20px', marginTop: '-25px' }}>
            <TextLink onClick={() => openPopup({ origin: 'modale' })}>Je ne sais pas quoi choisir</TextLink>
          </Center>
        </div> : <div />}
    </Modal>
    <Wrapper>
      <Block id="alterations">
        <Header2>
          <Link
            to={'/'} style={{ flex: 1, textAlign: 'center' }}
            onClick={() => trackEvent('click', 'home_step2')}
          >
            <Logo src={LogoImg} />
          </Link>
          <ProgressBar step={2} />
          <Title2>
            Je fais mon <Highlight>estimation</Highlight>
          </Title2>
          <Title3>
            Je choisis les retouches pour chacun de mes vêtements
          </Title3>
        </Header2>

        <ClothesList>
          {clothes.map((cloth, index) => {
            const clothNumber = clothes
              .filter((cloth2, index2) => cloth2.slug === cloth.slug && index >= index2)
              .length;
            return (<ClothAlterations
              clothNumber={clothNumber}
              cloth={cloth}
              toggleChoices={clothRef => setChoicesCloth(clothRef, cloth, index)}
              deleteCloth={() => deleteCloth(index, cloth.slug)}
              key={cloth.name}
            />);
          })}
          <Cloth onClick={() => browserHistory.push('/order/step1')}>
            <ClothText>Ajouter un</ClothText>
            <ClothText>vêtement</ClothText>
            <Add>+</Add>
          </Cloth>
        </ClothesList>

        <Total>Total : {total.toFixed(2)}€</Total>

        {hasError && <ErrorText>Merci de sélectionner des retouches pour tous les vêtements</ErrorText>}

        <Center>
          <Button3 isNotValid={!isButtonValid} onClick={goNext}>{'> suivant <'}</Button3>
        </Center>
        <Center>
          <TextLink onClick={() => openPopup({ origin: 'page' })}>
            Je ne sais pas quoi choisir</TextLink>
          <TextLinkSeparator>ou</TextLinkSeparator>
          <TextLink onClick={() => goNext({ validation: false })}>je veux passer cette étape</TextLink>
        </Center>
      </Block>
    </Wrapper>
  </div>
);

Step2.propTypes = {
  clothes: PropTypes.arrayOf(PropTypes.shape()).isRequired,
  toggleOrSetAlteration: PropTypes.func.isRequired,
  total: PropTypes.number.isRequired,
  showChoicesCloth: PropTypes.shape({}),
  showChoicesClothRef: PropTypes.shape({}),
  showChoicesItemIndex: PropTypes.number,
  openCategory: PropTypes.func.isRequired,
  openedCategory: PropTypes.string,
  setChoicesCloth: PropTypes.func.isRequired,
  close: PropTypes.func.isRequired,
  deleteCloth: PropTypes.func.isRequired,
  goNext: PropTypes.func.isRequired,
  isButtonValid: PropTypes.bool.isRequired,
  hasError: PropTypes.bool,
};

Step2.defaultProps = {
  showChoicesCloth: undefined,
  showChoicesClothRef: undefined,
  showChoicesItemIndex: undefined,
  openedCategory: undefined,
  hasError: false,
};

export default Step2;



